<template>
    <div class="video">
        <el-row :gutter="20">
            <el-col :span="6">
                <div class="grid-content ep-bg-purple"/>
                <div class="picture">
                    <img :src="himg" alt="">
                </div>
                <div class="main_title"/>
                <div class="main_fields">
                    <div class="main_input_fields">
                        <el-input v-model="keyword" :placeholder="placeholder"/>
                    </div>
                    <div class="main_fields_submit">
                        <el-button type="primary" @click="play">播放</el-button>
                        <el-button type="primary" @click="fullScrean">全屏</el-button>
                        <el-button type="primary" @click="help">帮助</el-button>
                    </div>
                </div>
            </el-col>
            <el-col :span="6">
                <div class="grid-content ep-bg-purple"/>
                <iframe src=""  ref="iframe" id="iframe"/>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import HeadImage from "@/assets/img/miss.jpg"

export default {

    components: {},
    data() {
        return {
            loginFlag: false,
            shang: false,
            himg: HeadImage,
            input_placeholder: '视频地址',
            search_btn: '搜索',
            play_btn: '播放',
            help_btn: '帮助',
            full_btn: '全屏',
            keyword: "",
            placeholder: "影视名称"
        }
    },
    mounted() {
    },
    methods: {
        help() {
            this.$router.push('/help')
        },
        play() {
            if (!this.keyword) {
                alert("请输入影视名称")
            } else {
                this.$refs.iframe.src=`https://svip.bljiex.com/?v=${this.keyword}`
            }
        },
        fullScrean() {
            if (!this.keyword) {
                alert("请输入影视名称")
            }
            const marioVideo = document.getElementById('iframe');
            if (marioVideo.requestFullscreen) {
                marioVideo.requestFullscreen();
            } else if (marioVideo.msRequestFullscreen) {
                marioVideo.msRequestFullscreen();
            } else if (marioVideo.mozRequestFullScreen) {
                marioVideo.mozRequestFullScreen();
            } else if (marioVideo.webkitRequestFullScreen) {
                marioVideo.webkitRequestFullScreen();
            }
        }

    }

}
</script>
<style lang="less" scoped>
#video {
  text-align: center;
  width: 100%;
}

#video-main {
  width: 100%;
  text-align: center;
  margin-right: 100px;
}


#valueurl {
  width: 180px;
}

#vipurl {
  width: 630px;
  height: 475px;
}

.weixin {
  z-index: 100;
}

.courage {
  color: red;
}

.result {
  margin-top: 520px;
}
#iframe{
    width: 800px;
    height: 500px;
}
</style>


